/* src/styles.css */

/* 全体のスタイル */
body {
  font-family: Arial, sans-serif;
  background-color: #f7f9fc;
  color: #333;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 800px;
  margin: 2rem auto;
  padding: 1rem;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #0070f3;
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

/* DiaryForm スタイル */
form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

input[type="text"],
textarea {
  padding: 0.8rem;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
}

textarea {
  min-height: 100px;
  resize: vertical;
}

button {
  padding: 0.8rem;
  font-size: 1rem;
  color: #fff;
  background-color: #0070f3;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #005bb5;
}

/* DiaryList スタイル */
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  background-color: #f1f5f9;
  border: 1px solid #e1e5ea;
  border-radius: 6px;
  padding: 1rem;
  margin-bottom: 1rem;
}

li h3 {
  font-size: 1.5rem;
  color: #333;
  margin: 0 0 0.5rem;
}

li p {
  margin: 0.5rem 0;
  color: #666;
}

li small {
  color: #999;
}

/* 検索機能 */
input[type="text"][placeholder="Search by tag"] {
  margin-bottom: 1rem;
  width: calc(100% - 100px);
}

button[type="button"] {
  margin-top: -0.2rem;
}
